





























































































































































































































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import moment from 'moment';

@Component({
  components: {
  },
  head: {
    title: {
      inner: '現在日時',
    },
  },
})
export default class Now extends Vue {
  // Data
  private dataNowMoment: any = moment(new Date());

  // Lifecycle hook
  created(): void {
    this.clock();
    console.log((new Date()).toString);
  }

  // Computed
  private get toIso8601(): string {
    return this.dataNowMoment.format();
  }

  // Methods
  private clock(): void {
    setInterval((): void => {
      this.dataNowMoment = moment(new Date());
    }, 10);
  }
}
